
<template>
  <div class="main main-center">
    <Title :title="country+titles[type]" />
    <div id="outbound"></div>
    <div class="tbas d-flex"> 
      <p 
      :class="type===item.type?'active':''"
      v-for="(item ,index) in topTenTabs" 
      @click="checkType(item.type)"
      >
        {{item.text}}
      </p>
    </div>
  </div>
</template>

<script>
import {exi} from '../existing';
import Title from '@/components/title'
export default {
  props:['historylist','country'],
  data() {
    return {
      topTenTabs: [
        { type: 0, text: '现存确诊趋势图' },
        { type: 1, text: '新增确诊趋势图' },
        { type: 2, text: '疫情累计趋势图' },
        { type: 3, text: '病死率治愈率' },
      ],
      titles:['疫情现存确诊趋势图','疫情新增确诊趋势图','疫情累计确诊趋势图','病死率、治愈率趋势图'],
      type:0
    }
  },
  mounted() {
    this.checkType(0);
  },
  components:{
    Title
  },
  methods: {
    checkType(type){
      this.type = type;
      let date = this.historylist.map(item => item.date);
      let myData = exi(type,this.historylist);
      let title = type !== 3?'人数':'百分比'
      this.$myecharts.mainlandMap("outbound",myData,date,true,title,true)
    }
  },
}
</script>
<style lang="less" scoped>
  .main{
    background-color:#FFF;
    padding:30px 0;
    #outbound{
      width:714px;
      height:714px;
    }
    .tbas{
    margin-top:30px;
    p{
      background: #fff;
      text-align: center;
      font-size: 24px;
      line-height: 40px;
      border-radius: 10px;
      position: relative;
      padding: 12px 28px;
      color: #808595;
      margin: 0 10px;
      flex: 1;
      border: 2px solid #D7DEEB;
      position: relative;
    }
    .active{
      background: #E0E8FF;
      color: #4D79F3!important;
      border-color: #BDCFFF;
    }
    .active::after{
      content: '';
      position: absolute;
      width: 0;
      height: 0;
      border: 2px solid #BDCFFF;
      border-width: 0 0 2px 2px;
      display: block;
      top: -10px;
      left: 50%;
      margin-left: -8px;
      background: #E0E8FF;
      transform: rotate(135deg);
      width: 10px;
      height: 10px;
    }
  }
  }
</style>