
<template>
  <div class="main">
    <div id="countries"></div>
  </div>
</template>
<script>
import {comparison} from '@/api/abroad'
export default {
  props: ['city','country'], 
  data() {
    return {

    }
  },

  mounted() {
    this.getWorldMap();
    
  },
  methods: {
    async getWorldMap() {
       let res = await comparison();
       let jsonName = res.filter(item => this.country === item.value)
      let existing = [];
      this.city.forEach(item => {
        item.mapName !== '' && existing.push({
          name: item.mapName,
          value: item.conNum,
          cityName:item.name,
          cureNum:item.cureNum,
          deathNum:item.deathNum,
        })
      });
      this.$worldecharts.countryMap('countries',jsonName[0].name,existing);
    }
  },
}
</script>
<style lang="less" scoped>
.main {
  #countries {
    margin:30px auto;
    background-color:#FFF;
    width: 714px;
    height: 600px;
    padding-bottom:20px;
  }
}
</style>