export function exi(type, list) {
  let covnum = [1, 1, 3, 2];
  let obj = {
    0: [
      { 
        name: '确诊', 
        color: '#FF3535', 
        data: list.map(item => item.conNum - item.cureNum - item.deathNum) 
      }
    ],
    1: [
      { 
        name: '新增确诊', 
        color: '#FF3535',
        data: list.map(item => item.conadd) 
      }
    ],
    2: [
      { 
        name: '确诊', 
        color: '#FF3535',
        data: list.map(item => item.conNum) 
      },
      { 
        name: '死亡', 
        color: '#2F2F2F',
        data: list.map(item => item.deathNum) 
      },
      { 
        name: '治愈', 
        color: '#13B593',
        data: list.map(item => item.cureNum)
      }],
    3: [
      { 
        name: '病死率', 
        color: '#2F2F2F',
        data: list.map(item => (item.deathNum / 1000000).toPrecision(2))
      },
      { 
        name: '治愈率', 
        color: '#13B593',
        data: list.map(item => (item.cureNum / 1000000).toPrecision(2))
      }
    ]
  }
  let existingArr = [];
  for (let i = 0; i < covnum[type]; i++) {
    existingArr.push({
      name: obj[type][i].name,
      color: obj[type][i].color,
      data: obj[type][i].data,
      type: 'line',
      smooth: true,
      symbolSize: 6,
      symbol: "circle",
    })

  }
  return existingArr
}