<template>
  <div class="main">
    <!-- banner -->
    <Banner :country="country" />
    <div class="cov-country main-center">
      <div class="cov-explain d-flex d-cont-center align-item">
        <span class="t_tit">{{countrydata.country}}</span>
        <span>{{countrydata.times}}(北京时间）数据统计</span>
      </div>
      <!-- 累计数据 -->
      <div class="tabs d-flex d-cont-arund d-cont-center">
          <div 
          v-for="(item ,index) in cumulativeArr" 
          :key="index"
          class="tab d-flex d-flex-column align-item"
          >
              <p class="title">{{item.text}}</p>
              <p class="cumula" :style="'color:' + item.color">{{item.cumula}}</p>
              <p class="yesterday">较昨日<i>{{item.yesterday}}</i></p>
          </div>
      </div> 
    </div>
    <!-- 跳转核酸检测机构 -->
    <testing />
    <div class="vantabs">
      <van-tabs v-model="active" title-class="vantab">
      <van-tab title-class="vantab" title="疫情地图"></van-tab>
      <van-tab title-class="vantab" title="疫情资讯"></van-tab>
    </van-tabs>
    </div> 
    <Countries v-if="city.length !==0" :country="country" :city="city" />

    <!-- 疫情趋势图 -->
    <ExistingChart 
    v-if="historylist.length !==0 " 
    :historylist="historylist"
    :country="country"
    />

    <!-- 疫情资讯 -->
    <Progress />
    <!-- 疫情列表 -->
    <epidemicList v-show="city.length !==0" :city="countrydata.city" :title="countrydata.country"/>
    <!-- 疫情资讯 -->
    <Information :country="country" :citycode="citycode"/>
  </div>
</template>

<script>
import {abroad} from '@/api/abroad';
import testing from '@/components/testing';
import epidemicList from './components/epidemicList';
import Countries from './components/countries';
import Progress from '@/components/Progress';
import ExistingChart from './components/existingChart';
import Banner from './components/banner/banner';
import Information from './components/information';

export default {
  data() {
    return {
      citycode:'',
      countrydata:[],
      cumulativeArr:[],
      active:0,
      city:[],
      country:'',
      historylist:[]
    }
  },
  //生命周期 - 挂载完成（访问DOM元素）
  mounted() {
    let {citycode} = this.$route.params;
    this.citycode = citycode;
    this.getAbroad(citycode);
    this.$nextTick(() => this.$bus.$on('nocityCode',e=>{
      this.getAbroad(e);
    }))
  },
  methods: {
    async getAbroad(citycode){
      let res = await abroad(citycode);
      let {city,historylist,country} = res.data;
      this.cumulative(res.data);
      this.city = city;
      this.historylist = historylist;
      this.countrydata = res.data;
      this.country = country;
    },
    cumulative(res){
      this.cumulativeArr.push(
        {
          text:'累计确诊',
          cumula:res.contotal,
          yesterday:res.adddaily.conadd,
          color:'#B10000',
        },
        {
          text:'累计治愈',
          cumula:res.curetotal,
          yesterday:res.adddaily.cureadd,
          color:'#13B593',
        },
        {
          text:'累计死亡',
          cumula:res.deathtotal,
          yesterday:res.adddaily.deathadd,
          color:'#666666',
        }
      )
    },

  },
  components:{
    testing,
    epidemicList,
    Progress,
    ExistingChart,
    Countries,
    Banner,
    Information
  }
}
</script>
<style lang="less" scoped>
  .cov-country{
    .cov-explain{
      padding:25px 0 20px;
      height:80px;
      line-height:35px;
      text-align: center;
      color: #777;
      font-size: 24px;
      .t_tit{
        background-color:#4d79f3;
        display: inline-block;
        line-height:35px;
        height: 35px;
        color: #fff;
        font-size:26px;
        border-radius:10px;
        margin-right:10px;
        font-weight: 700;
        padding: 0 12px;
      }
    }
    .tabs{
      background-color: #fff;
      padding: 30px 0;
      .tab{
        .title{
              font-size:20px;
              font-weight: 400;
              color: #333;
              line-height: 40px;
          
        }
        .cumula{
          font-weight: bold;
          font-size:42px;
        }
        .yesterday{
         
          line-height:40px;
          font-size:20px;
          color: #999;
          i{
            font-weight: bold;
            font-size: 0.24rem;
            color: #666;
          }
        }
      }
    }

  }
  .vantabs{
      height:77px;
      background-color:#FFF;
      padding-top:14px;
    }
</style>